.topProductPage-container {
    padding: 25px 180px;
  }
  
  .topProductPage-texts {
    margin-bottom: 40px;
  }
  
  .topProductPage-p {
    margin-bottom: 3px;
    color: #777;
    font-family: "Roboto", sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.667;
  }
  
  .topProductPage-h2 {
    font-size: 36px;
    font-weight: 600;
    font-family: "Oswald", sans-serif;
    color: #222;
    line-height: 1.1;
  }
  
  .topProductPage-span {
    border-bottom: 2px solid #384aeb;
    padding-bottom: 8px;
  }
  
  .topProductPage-cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* default: 4 kart */
    gap: 20px;
  }
  
  /* Kart stili */
  .topProduct-item {
    border: 1px solid #EEEEEE;
    padding: 15x;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  /* Responsive: 1199px və aşağıda 2 kart */
  @media (max-width: 1199px) {
    .topProductPage-cards {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media(max-width:981px){
    .topProductPage-container {
        padding: 25px 100px;
      }

  }
  /* Mobil: 566px və aşağıda 1 kart */
  @media (max-width: 566px) {
    .topProductPage-cards {
      grid-template-columns: 1fr;
    }
  }
  